@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500;700&display=swap);
@import url(https://fonts.googleapis.com/css?family=Muli);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  box-sizing: border-box;
}

:root {
  --accent-color: #2c31cf;
  --accent-bg-hover: #1e266d;
}

* {
  box-sizing: border-box;
  font-family: 'Muli', sans-serif;
}

/* .Layout-Layout {
  background-color: white;
} */

h1, h2, h3, h4, h5, h6, button span {
  font-weight: 700;
}


.Header-Header {
    background-color: white;
    box-shadow: 0 0 10px #ccc;
    position: fixed;
    width: 100%;
    top: 0;
    padding: 0 12px;
    display: flex;
    justify-content: space-between;
    flex-direction: row;
    transition: all 0.3s ease-in;
    z-index: 100;
}

.Header-Header-Idle {
    background-color: transparent;
    height: 80px;
    box-shadow: none;
}

.Header-Header img {
    height: 36px;
    margin: auto 18px auto 12px;
}

@media (max-width: 991px) {
    .Header-Header .NavItem {
        display: none;
    }

    .Header-Header .NavItem:first-child {
        display: inline-block;
        margin: 0;
        padding: 0;
    }
}

.NavItems-NavItems {
    display: flex;
    flex-direction: row;
    padding: 0;
}

.NavItems-NavItems li {
    list-style: none;
    margin: 0 12px;
    box-sizing: border-box;
}

.NavItems-NavItems a {
    padding: 21px 6px;
    color: var(--accent-bg-hover);
    font-size: 16px;
    border-bottom: 2px solid transparent;
    font-weight: 700;
}

.NavItems-NavItems a:hover, 
.NavItems-NavItems a.active {
    color: var(--accent-color);
    border-color: var(--accent-color);
}

.NavItem:first-child a:hover {
    border: none;
}

.ant-drawer-body {
    padding: 0;
}

.Draw-Logo {
    margin: 18px;
}

@media (max-width: 991px) {
    .NavItems-NavItems {
        flex-direction: column;
    }
    
    .NavItems-NavItems li {
        margin: 6px 18px;
        width: calc(100% - 36px);
        padding-bottom: 12px;
        border-bottom: 1px solid #ddd;
    }
    
    .NavItems-NavItems li:first-child {
        border: none;
    }

    .NavItems-NavItems a {
        padding: 21px 6px;
        color: #3B3C54;
        border: none;
    }
    
    .NavItems-NavItems a:hover, 
    .NavItems-NavItems a.active {
        color: var(--accent-color);
    }
}
.SideDrawMenu {
    width: 40px;
    height: 40px;
    margin: 12px 0;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    cursor: pointer;
}

.SideDrawMenu-div {
    width: 80%;
    border-bottom: 3px solid white;
    margin: auto;
    transition: all 0.3s linear;
}

.SideDrawMenu-div-idle {
    border-color: white;
}

/* .SideDrawMenu-Close {
    margin-right: 17px;
} */

.SideDrawMenu-Close div:nth-child(1) {
    transform: translateY(13px) rotate(135deg);
}

.SideDrawMenu-div-idle {
    border-color: #333;
}

.SideDrawMenu-Close div:nth-child(2) {
    transform: rotate(45deg);
}

.SideDrawMenu-Close div:nth-child(3) {
    transform: translateY(-13px) rotate(135deg);
}

@media (min-width: 992px) {
    .SideDrawMenu {
        display: none;
    }
}
.Footer-Footer {
    padding: 12px;
    color: var(--accent-bg-hover);
    background-color: white;
    width: 100%;
}

.Footer-Row {
    padding: 35px 80px;
    margin: auto;
}

.Footer-Footer:first-child p {
    width: 75%;
    margin-bottom: 0;
}

.Footer-codelearn-img {
    height: 60px;
    margin-bottom: 18px;
}

.Footer-Icons {
    display: flex;
    padding: 12px 0;
    font-size: 30px;
}

.Footer-Icon {
    margin-right: 6px;
}

.Footer-Icon:nth-child(1) {
    color: #2C477E;
}

.Footer-Icon:nth-child(2) {
    color: #B21010;
}

.Footer-Icon:nth-child(3) {
    color: #0081C7;
}

.Footer-Icon:nth-child(4) {
    color: #BF1B8F;
}

.Footer-Info-Title {
    color: var(--accent-color);
    margin: 18px 0;
}

.Footer-Info ul {
    list-style: none;
    padding: 0;
}

.Footer-Info li {
    margin-bottom: 12px;
}

.Footer-Info a {
    text-decoration: none;
    color: var(--accent-bg-hover);
}

.Footer-Info a:hover {
    text-decoration: underline;
}

.Footer-Footer:last-child {
    display: flex;
    justify-content: center;
    background-color: #1C1D31;
}

.Footer-Footer:last-child img {
    margin-right: 18px;
}

.Footer-Footer:last-child p {
    margin: auto 0;
    color: white;
}

.Footer-Footer:last-child a {
    color: white;
}

.Footer-Footer:last-child a:hover {
    text-decoration: underline;
}

@media (max-width: 991px) {
    .Footer-Row {
        padding: 18px;
    }
    .Footer-Footer:first-child p {
        width: 100%;
    }
    .Footer-Icons {
        margin-bottom: 12px;
    }
}
.RegisterContent {
    padding-top: 80px;
    background-image: url(data:image/png;base64,);
    background-position: top right;
    background-repeat: no-repeat;
    background-size: 35%;
    margin-bottom: 40px;
}

.RegisterContent-Left {
    padding: 0 2%;
    min-height: 300px;
    display: flex;
}

.RegisterContent img {
    width: 90%;
    margin: auto;
}

.RegisterContent-Title {
    width: 100%;
    max-width: 600px;
    min-height: 240px;
    background-color: white;
    position: absolute;
    bottom: 15%;
    right: 0;
    padding: 25px 35px;
}

.RegisterContent-Title h1 {
    font-size: 44px;
    color: var(--accent-color);
    margin: 0;
}

.RegisterContent-Title p {
    font-size: 20px;
    margin: 0;
    color: var(--accent-bg-hover);
}

.RegisterContent-Right {
    padding: 0 48px;
    display: flex;
}

.RegisterContent-Form {
    width: 100%;
    margin: auto;
    border-radius: 25px;
    padding: 25px;
    box-shadow: 0 0 5px #cdcdcd;
    background-color: white;
}

.RegisterContent-Form p {
    font-size: 16px;
    text-align: center;
}

.RegisterContent-LoginBtn {
    display: flex;
    justify-content: center;
}

.RegisterContent-LoginBtn button {
    margin: 0 12px;
    font-size: 24px;
    height: 60px;
    border-radius: 50%;
    cursor: not-allowed;
}

.RegisterContent-LoginBtn button:nth-child(1) {
    color: #00A4F5;
    border-color: #00A4F5;
}

.RegisterContent-LoginBtn button:nth-child(1):hover {
    color: white;
    background-color: #00A4F5;
}

.RegisterContent-LoginBtn button:nth-child(2) {
    color: #36589D;
    border-color: #36589D;
}

.RegisterContent-LoginBtn button:nth-child(2):hover {
    color: white;
    background-color: #36589D;
}

.RegisterContent-LoginBtn button:nth-child(3) {
    color: #EA230F;
    border-color: #EA230F;
}

.RegisterContent-LoginBtn button:nth-child(3):hover {
    color: white;
    background-color: #EA230F;
}

@media (max-width: 991px) {
    .RegisterContent-Right {
        padding: 0 18px;
    }
}

@media (max-width: 575px) {
    .RegisterContent img {
        opacity: 0;
    }

    .RegisterContent-Title {
        bottom: 0;
        padding: 20px;
        text-align: center;
    }
}





.RegisterForm h1 {
    color: var(--accent-bg-hover);
}

.RegisterForm-Input {
    padding: 12px 20px;
    margin-bottom: 18px;
