.StatisticLanding {
    background-attachment: fixed;
    background-image: url(/codelearn/static/media/bg-statistic.f1e87ad5.png);
    background-size: cover;
    background-repeat: no-repeat;
    padding: 80px 0;
    justify-content: space-between;
}

.StatisticLanding-Row {
    width: 90%;
    max-width: 1200px;
    margin: auto;
}

.StatisticLanding-Item {
    padding-bottom: 48px;
