.Question-Question {
    margin-left: 200px;
    padding: 1rem 3rem 60px;
}

.Question-Detail-Container {
    display: flex;
    margin-bottom: 2rem;
    margin-top: 1rem;
}

.Question-Detail-Order {
    display: flex;
    width: 60px;
    height: 60px;
    background: linear-gradient(45deg, #eec83d 0%, #86cbcc 100%);
    border-radius: 50%;
    margin: 0 auto;
}

.Question-Detail-Order h2 {
    margin: auto;
    color: #333;
}

.Question-Detail {
    margin: auto 1rem;
    flex: 1 1;
}

.Question-Detail h3 {
    margin-bottom: 0;
}

.Question-Answer {
    display: block;
    height: 30px;
    line-height: 30px;
}

.Question-Ans {
    border: 1px solid #d9d9d9;
    width: 90%;
    margin: 1rem auto;
    display: flex;
    padding: 1rem;
    border-radius: 8px;
    cursor: pointer;
    box-sizing: border-box;
}

.Question-Ans p {
    font-size: 18px;
    margin: auto 1rem;
    color: rgba(0, 0, 0, 0.85);
}

.Question-Ans:hover {
    border-color: #1890ff;
} 

.Question-Ans:hover p {
    color: #1890ff;
}

.Question-Ans-Active {
    background: linear-gradient(45deg, #dab635 0%, #86cbcc 100%);
}

.Question-Ans.Question-Ans-Active p {
    font-weight: 700;
}

.Question-Ans-Active:hover {
    border-color: #d9d9d9;
}

.Question-Ans-Active:hover p{
    color: rgba(0, 0, 0, 0.85);
}

@media (max-width: 768px) {
    .Question-Question {
        margin-left: 0;
        padding-left: 24px;
        padding-right: 8px;
    }
}

@media (max-width: 576px) {
    .Question-Detail-Container {
        flex-direction: column;
    }

    .Question-Detail-Order {
        width: 60%;
        margin: auto;
